.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  overflow: hidden;
}

.overlayVisible {
  visibility: visible;
  opacity: 1;
  .drawer {
    transform: translateX(0);
  }
}

.drawer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;
  position: fixed;
  overflow: auto;
  width: 420px;
  right: 0;
  height: 100%;
  background: #ffffff;
  box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
  transform: translateX(100%);
  transition: opacity 0.3s ease-in-out;
  img {
    cursor: pointer;
  }
  h2 {
    display: flex;
    justify-content: space-between;
    margin: 0;
    margin-bottom: 40px;
  }
  .items {
    flex: 1;
    overflow: auto;
    margin-bottom: 40px;
  }
}
